.info-container {
    border-radius: 6px;
    box-sizing: border-box;
    color: white;
    width: 100%;
    max-width: 60rem;
    height: 16rem;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    margin: 0 auto;
    opacity: 0.95;
    box-shadow: 0 10px 10px rgba(0, 0, 0, 0.2);
}

.graditude-info {
    margin-top: 10px;
}

.tournament-info {
    background-color: #131319;
    border-radius: 6px;
    box-sizing: border-box;
    color: white;
    margin: 0 auto;
}

.background-image {
    width: 100%;
    max-width: 1200px;
    position: absolute;
    left: 50%;
    transform: translateX(-50%);
    z-index: 0;
}

/* Стили для контейнеров с контентом */
.image-container {
    flex-direction: column;
    align-items: center;
    justify-content: center;
    width: 100%;
    z-index: 1;
    text-align: center;
}

.info_title, .discrip {
    text-align: center;
    margin: 0.8rem auto;
    width: 90%;
    max-width: 800px;
}

.image-container2 {
    background-color: #131319;
    border-radius: 6px;
    width: 100%; /* Изменено с 18rem */
    max-width: 18rem; /* Добавлено ограничение максимальной ширины */
    height: 16rem;
    align-items: center;
    margin: 0 auto; /* Изменено с margin-left: 4rem */
    z-index: 1; 
    opacity: 0.95;
}

/* Добавьте это в начало вашего CSS */
html {
    box-sizing: border-box;
}
*, *:before, *:after {
    box-sizing: inherit;
}

/* Исправленные проблемные элементы */
.image-container3 {
    border-radius: 6px;
    width: 100%; /* Изменено с 85rem */
    max-width: 85rem; /* Добавлено ограничение максимальной ширины */
    height: auto; /* Изменено с фиксированной высоты */
    min-height: 15rem;
    border: 9px solid rgba(19, 19, 25, 1);
    margin: 0 auto; /* Центрирование */
}
/* Стили для кнопок и навигации */
.button-list {
    width: 100%;
    margin: 0.8rem 0;
    overflow-x: auto; /* Добавляем горизонтальный скролл при необходимости */
    -webkit-overflow-scrolling: touch; /* Плавный скролл на iOS */
    white-space: nowrap; /* Запрещаем перенос кнопок */
    z-index: 1;
    text-align: center;
}

.button-container {
    margin-left: 0; /* Изменено с 20rem */
    width: 100%;
    padding: 0 1rem;
}

.button-list ul {
    list-style: none;
    padding: 0;
    display: inline-flex; /* Изменено с flex на inline-flex */
    justify-content: flex-start; /* Изменено с space-around */
    margin: 0;
    min-width: min-content; /* Минимальная ширина по содержимому */
}

.button-list li {
    margin: 0 4px 0 0;
    color: rgba(255, 255, 255, 0.664);
    border-radius: 3px;
    background: #25272D;
    position: relative;
    flex-shrink: 0; /* Запрещаем уменьшение кнопок */
}

.button-list button {
    height: 44px;
    min-width: 80px;
    width: 16.9rem;
    padding: 0 10px;
    border: none;
    text-decoration: none;
    transition: 0.4s;
    color: #AFAEB4;
    font-size: 19.2px;
    display: flex;
    justify-content: center;
    align-items: center;
    font-weight: bold;
    cursor: pointer;
    z-index: 1;
    flex-shrink: 0; /* Запрещаем уменьшение кнопок */
}

.button-list button:hover {    
    transform: scale(1);    
    color: white;    
    z-index: 1; 
}

.button-list-line {
    width: 100%;
    height: 1.5px;
    background: #a855f7;
    position: absolute;
    bottom: 0;
}

.tournament-header {
    margin-top: 8px;
    width: 100%;
    color: white;
    background-color: #0A0A0A;
    border-radius: 6px;
    display: flex;
    justify-content: center;
    margin-bottom: 0.8rem;
}

/* Стили для контента */
.content-with-guests {
    width: 100%;
    max-width: 1200px;
    margin: 8px auto;
    display: flex;
    flex-direction: column;
    align-items: center;
    position: relative;
    z-index: 1;
    opacity: 0.95;
}

.info-sections-container {
    display: flex;
    justify-content: center;
    width: 100%;
    padding: 15px;
    box-sizing: border-box;
    flex-wrap: wrap;
}

.info-section {
    flex: 1;
    min-width: 300px;
    max-width: 400px;
    margin: 8px;
    background-color: #1a1a1f;
    border-radius: 6px;
    padding: 15px;
    display: flex;
    flex-direction: column;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.3);
}

.section-title {
    color: #a855f7;
    font-size: 1.3rem;
    margin-bottom: 12px;
    text-align: center;
}

.section-divider {
    height: 2px;
    background: linear-gradient(90deg, transparent, #a855f7, transparent);
    margin-bottom: 12px;
}

.section-content {
    flex-grow: 1;
    line-height: 1.5;
}

.section-content p {
    margin-bottom: 8px;
    font-size: 18px;
}

.specific-list {
    color: white;
    background-color: #131319;
    border-radius: 6px;
    align-items: flex-start;
    width: 100%; /* Изменено с 85rem */
    max-width: 85rem; /* Добавлено ограничение максимальной ширины */
    height: auto; /* Изменено с фиксированной высоты */
    min-height: 48rem;
    z-index: 1;
    opacity: 0.95;
    padding: 20px;
    box-sizing: border-box;
    margin: 0 auto; /* Центрирование */
}

/* Стили для формы авторизации */
.auth-form {
    display: flex;
    flex-direction: column;
    align-items: center;
    background-color: #131319;
    border-radius: 6px;
    color: white;
    padding: 1.5rem;
    margin: 2rem auto;
    width: 90%;
    max-width: 400px;
    box-shadow: 0 3px 6px rgba(0, 0, 0, 0.2);
    z-index: 10;
    position: fixed;
    top: 50%;
    left: 50%;
    transform: translate(-50%, -50%);
}

.auth-input input {
    width: 75%;
    height: 2.2rem;
    padding: 0 0.8rem;
    margin-bottom: 0.8rem;
    background-color: #25272D;
    color: white;
    font-size: 0.9rem;
}

.auth-input button {
    width: 45%;
    height: 2.2rem;
    background-color: #3AA1FF;
    color: white;
}

/* Адаптация для планшетов (768px и меньше) */
@media (max-width: 768px) {
    .info-container {
        height: auto;
        padding: 1rem;
    }
    
    .image-container2 {
        max-width: 100%; /* Разрешаем занимать всю ширину */
    }
    /* Добавьте это для кнопок */
    .button-list ul {
        flex-wrap: nowrap;
        overflow-x: auto;
        -webkit-overflow-scrolling: touch;
        justify-content: flex-start;
        padding-bottom: 10px;
    }
    .button-container {
        margin-left: 0;
    }
    
    .button-list li {
        flex: 0 0 auto;
    }
    
    .button-list ul {
        flex-wrap: nowrap; /* Убедитесь, что перенос запрещен */
    }
    
    .button-list button {
        width: 14rem; /* Немного уменьшаем ширину кнопок */
    }
    
    .content-with-guests {
        margin: 8px 0;
    }
    
    .image-container3 {
        width: 100%;
        height: auto;
        border-width: 5px;
    }
    
    .specific-list {
        width: 100%;
        height: auto;
        padding: 15px;
        min-height: 300px;
    }
    
    .info-sections-container {
        flex-direction: column;
        align-items: center;
    }
    
    .info-section {
        max-width: 100%;
        margin: 8px 0;
        min-width: 250px;
    }

    .auth-form {
        width: 90%;
        padding: 1rem;
    }
}

/* Адаптация для мобильных (480px и меньше) */
@media (max-width: 405px) {
    .info-container {
        padding: 0.8rem;
        height: auto;
    }
    
    .image-container2 {
        width: 95%;
        margin: 0.5rem auto;
    }
    
    .auth-form {
        width: 95%;
        padding: 1rem;
    }
    
    .auth-input input, 
    .auth-input button {
        width: 90%;
        font-size: 0.8rem;
    }
    
    .section-title {
        font-size: 1.1rem;
    }
    
    .section-content p {
        font-size: 0.9rem;
        line-height: 1.4;
    }
    
    .tournament-header {
        padding: 0.5rem;
        font-size: 0.9rem;
    }

    .specific-list {
        padding: 10px;
    }

    .info-section {
        padding: 10px;
        margin: 6px 0;
    }
    .button-list button {
        width: 12rem; /* Еще уменьшаем на очень маленьких экранах */
        font-size: 16px; /* Уменьшаем размер шрифта */
    }
}

/* Адаптация для очень маленьких экранов (320px и меньше) */
@media (max-width: 320px) {
    .button-list li {
        flex: 1 1 120px;
    }
    
    .button-list button {
        font-size: 12px;
    }
    
    .section-title {
        font-size: 1rem;
    }
    
    .section-content p {
        font-size: 0.8rem;
    }
}