.pick-and-ban-container {
    padding: 20px;
    max-width: 1200px;
    margin: 0 auto;
    font-family: Arial, sans-serif;
}

.banned-section {
    margin-bottom: 30px;
    padding: 15px;
    border-radius: 8px;
    background-color: #f5f5f5;
}

.main-section {
    display: flex;
    gap: 20px;
}

.left-column, .right-column {
    flex: 1; /* Одинаковый flex-grow */
    min-width: 0; /* Предотвращает "распирание" контента */
    display: flex;
    flex-direction: column;
    gap: 20px;
}

.status-grid {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(120px, 1fr));
    gap: 10px;
}

.status-column {
    display: flex;
    flex-direction: column;
    gap: 10px;
    border: 2px solid #e0e0e0;
    border-radius: 8px;
    padding: 10px;
    background-color: #fafafa;
}

.status-header {
    padding: 10px;
    border-radius: 4px;
    font-weight: bold;
    text-align: center;
    background-color: #4CAF50;
    color: white;
}

.status-item {
    padding: 8px 15px;
    border-radius: 4px;
    cursor: pointer;
    transition: all 0.2s;
    font-weight: bold;
    text-align: center;
}

.available {
    background-color: #e0e0e0;
    color: #333;
    border: 1px solid #ccc;
}

.available:hover {
    background-color: #d0d0d0;
}

.banned {
    background-color: #ff6b6b;
    color: white;
}

.identities-list {
    display: grid;
    grid-template-columns: repeat(auto-fill, minmax(150px, 1fr));
    grid-auto-rows: minmax(180px, auto); /* Фиксированная высота строк */
    gap: 15px;
    max-height: 500px;
    overflow-y: auto;
    padding: 10px;
}
