/* TestComponent.css */

.container {
  margin-bottom: 20px;
}

.toggleButton {
  padding: 10px 20px;
  color: white;
  border: none;
  border-radius: 4px;
  cursor: pointer;
  font-size: 16px;
}

.toggleButtonV1 {
  background-color: #f44336;
}

.toggleButtonV2 {
  background-color: #4CAF50;
}

.versionText {
  margin-left: 10px;
}

.descriptionTextarea {
  margin-top: 30px;
  margin-bottom: 50px;
  font-size: 24px;
  font-weight: 500;
}

.descriptionContainer {
  width: 1100px;
  padding: 20px;
  background-color: #131319;
  margin-bottom: 50px;
}