.tournament-grid {
  font-family: Arial, sans-serif;
  max-width: 1200px;
  margin: 0 auto;
  padding: 20px;
}

.tournament-grid h1 {
  text-align: center;
  color: #333;
}

.grid-container {
  display: flex;
  justify-content: space-between;
  margin-top: 30px;
  position: relative;
}

.round {
  flex: 1;
  padding: 0 10px;
  display: flex;
  flex-direction: column;
}

.round h3 {
  text-align: center;
  margin-bottom: 20px;
  color: #555;
}

/* Вертикальное выравнивание для каждого раунда */
.round.quarter-finals {
  justify-content: space-around;
}

.round.semi-finals {
  justify-content: center;
}

.round.final {
  justify-content: center;
}

.round.champion {
  justify-content: center;
}

.match {
  background: #f5f5f5;
  border-radius: 5px;
  padding: 10px;
  margin-bottom: 20px;
  position: relative;
}

.player {
  padding: 8px;
  margin: 5px 0;
  background: white;
  border: 1px solid #ddd;
  border-radius: 3px;
  color: #333;
}

.match-date {
  color: #333;
}

.ssilka {
  color: #a057e0;
}

.champion-box {
  padding: 15px;
  background: rgb(226, 237, 80);
  border-radius: 5px;
  text-align: center;
  font-weight: bold;
  font-size: 1.2em;
  border: 2px solid #ad3ac2;
}

.round-titles {
  display: flex;
  justify-content: space-between;
  padding: 0 10px;
  margin-top: 20px;
}

.round-titles h3 {
  flex: 1;
  text-align: center;
  margin: 0 10px;
  color: #ffffff;
}

.name h1 {
  flex: 1;
  text-align: center;
  margin: 0 10px;
  color: #ffffff;
}

/* Responsive adjustments */
@media (max-width: 768px) {
  .tournament-grid {
    padding: 10px;
  }
  
  .grid-container {
    flex-direction: column;
    margin-top: 15px;
  }
  
  .round {
    margin-bottom: 15px;
    padding: 0 5px;
    width: 100%;
  }
  
  .round h3 {
    margin-bottom: 10px;
    font-size: 1.1em;
  }
  
  .match {
    margin-bottom: 15px;
    padding: 8px;
  }
  
  .player {
    padding: 6px;
    font-size: 0.9em;
  }
  
  .champion-box {
    padding: 10px;
    font-size: 1em;
    margin-top: 10px;
  }
  
  .round-titles {
    flex-direction: column;
    margin-top: 10px;
  }
  
  .round-titles h3 {
    margin: 5px 0;
  }
  
  /* Скрываем лишние элементы на мобильных */
  .round .match::before,
  .round .match::after,
  .round .champion-box::before {
    display: none;
  }
}

/* Дополнительные стили для очень маленьких экранов */
@media (max-width: 480px) {
  .tournament-grid h1 {
    font-size: 1.5em;
  }
  
  .match {
    padding: 5px;
  }
  
  .player {
    padding: 4px;
    font-size: 0.8em;
  }
  
  .champion-box {
    font-size: 0.9em;
    padding: 8px;
  }
}