.tier-list-nav-container {
  width: 90%;
  margin-bottom: 36px;
  margin-top: -10px;
}

.tier-list-nav {
  width: 100%;
}

.tier-list-nav ul {
  list-style: none;
  padding: 0;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  width: 100%;
  justify-content: flex-start;
  margin: 0;
}

.tier-list-nav li {
  width: fit-content;
  height: fit-content;
  margin-bottom: 8px;
  margin: 0 4.8px 0px 0px;
  color: rgba(255, 255, 255, 0.664);
  border-radius: 3.2px;
  background: #25272D;
  position: relative;
  box-sizing: border-box;
}

.changelog-button {
  background: none;
  border: none;
  color: inherit;
  cursor: pointer;
  padding: 0.63rem 1rem;
  font-size: 1.3rem;
  transition: color 0.3s ease;
}

.changelog-button:hover {
  color: #4a90e2;
}

.changelog-panel {
  position: fixed;
  top: 50%;
  left: 50%;
  transform: translate(-50%, -50%);
  background: #1C1E23;
  padding: 2rem; /* Увеличен padding */
  border-radius: 8px;
  box-shadow: 0 4px 20px rgba(0, 0, 0, 0.5);
  z-index: 1001;
  max-width: 700px; /* Увеличен max-width */
  width: 95%; /* Увеличен width */
  max-height: 85vh; /* Увеличен max-height */
  overflow-y: auto;
}

.changelog-header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 1rem;
  border-bottom: 1px solid #444;
  padding-bottom: 0.5rem;
  color: #ffffff;
}

.close-button {
  background: none;
  border: none;
  color: #aaa;
  font-size: 2.5rem;
  cursor: pointer;
  line-height: 1;
  padding: 0 0.5rem;
}

.close-button:hover {
  color: #4a90e2;
}

.changelog-item {
  background: #25272D;
  padding: 1.5rem; /* Увеличено с 1rem */
  margin-bottom: 1.5rem; /* Увеличено с 1rem */
  border-radius: 8px; /* Увеличено с 6px */
}

.changelog-date {
  font-weight: bold;
  margin-bottom: 0.75rem; /* Увеличено с 0.5rem */
  color: #4a90e2;
  font-size: 1.2rem; /* Добавлено, если нужно увеличить шрифт */
}

.changelog-backdrop {
  position: fixed;
  top: 0;
  left: 0;
  right: 0;
  bottom: 0;
  background: rgba(0, 0, 0, 0.7);
  backdrop-filter: blur(5px);
  z-index: 1000;
}

.description-section {
  margin-bottom: 1.25rem; /* Увеличено с 1rem */
}


.section-header:hover {
  background: #3A3D44;
}
.section-header {
  color: #ffffff;
  position: relative;
  padding-right: 40px; /* Увеличено с 35px */
  cursor: pointer;
  min-height: 35px; /* Увеличено с 30px */
  font-size: 1.2rem; /* Добавлено для увеличения шрифта */
}
.section-content {
  padding: 1.25rem; /* Увеличено с 1rem */
  margin-top: 0.75rem; /* Увеличено с 0.5rem */
  background: #1E2025;
  border-radius: 6px; /* Увеличено с 4px */
  line-height: 1.6; /* Увеличено с 1.5 */
  color: #ffffff;
  font-size: 1.1rem; /* Добавлено для увеличения шрифта */
}

.large-symbol {
    font-size: 1.5em;
    font-weight: bold;
    padding: 0 1px;
    border-radius: 0.2em;
    
}
.expand-toggle {
  position: absolute;
  right: 0;
  top: 50%;
  transform: translateY(-50%); /* Вертикальное центрирование */
  float: none; /* Убираем float */
  margin-left: 10px;
  font-weight: bold;
  color: #4a90e2;
  transition: transform 0.2s;
}

.large-symbol--plus {
    color: #4CAF50; /* Green color for plus */
}
.large-symbol--plusminus { 
  color: #fad200; /* Green color for plus */
}
.large-symbol--minus {
    color: #F44336; /* Red color for minus */
    font-size: 2em;
    vertical-align: middle;
}

.entity-link {
  color: #4a90e2;
  text-decoration: none;
  transition: color 0.2s;
}

.entity-link:hover {
  color: #6ba8ff;
  text-decoration: underline;
}

.status-icon {
  height: 15px;
  width: auto;
}

.entity-icon {
  width: 35px; /* Увеличено с 30px */
  height: 35px; /* Увеличено с 30px */
  vertical-align: middle;
  margin-right: 6px; /* Увеличено с 4px */
  object-fit: contain;
  margin: 3px; /* Увеличено с 2px */
  border: 4px solid rgba(19, 19, 25, 1); /* Увеличено с 3px */
}

.tier-list-nav a {
  height: 44px;
  min-width: 80px;
  width: fit-content;
  padding: 0 10px;
  text-decoration: none;
  transition: 0.4s;
  color: #AFAEB4;
  font-size: 19.2px;
  display: flex;
  justify-content: center;
  align-items: center;
  font-weight: bold;
}

.tier-list-nav a:hover {
  color: #fff;
}

.tier-list-nav--active {
  background: linear-gradient(0deg, #3AA1FF -139.584%, #211E29 25.72%);
  color: #fff !important;
}

.tier-list-nav-line {
  width: 100%;
  height: 1.6px;
  background: #3AA1FF;
  position: absolute;
  bottom: 0;
}

body.no-scroll {
  overflow: hidden;
}