.MDcrafts-wrapper{
    width: 100%;
    color: white;
    display: flex;
    flex-direction: column;
    gap: 15px;
}
.filters2 {
  width: 90%;
  margin-bottom: 5.4px;
  position: relative;
  user-select: none;
}
.filters-main-header2 {
  font-size: 32px;
  font-weight: 600;
  color: white;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  margin-bottom: 5.4px;
}
.filters-frame-line {
    width: 100%;
    height: 1.6px;
    background: #3AA1FF;
    position: absolute;
    bottom: 0;
    left: 0;
}


.filters-main-header--collapse {
  margin-bottom: 0px;
}
.filters-main2 {
  display: unset;
  flex-wrap: wrap;
  column-gap: 19.2px;
  row-gap: 35px;
}
.btn-filters {
  display: flex;
  align-items: center;
  color: rgba(255, 255, 255, 0.8);
  background-color: #25272D;
  padding: 10px;
  box-sizing: border-box;
  font-weight: 400;
  gap: 8px;
  transition: 0.4s;
  border: none;
  cursor: pointer;
  border-radius: 4px;
}
.show-filters {
  display: none;
  font-size: 16px;
  height: 43.6px;
}
.btn-filters svg {
  width: 23.6px;
  height: 23.6px;
}
.btn-filters path {
  fill: rgba(255, 255, 255, 0.801);
  transition: 0.4s;
}
.show-filters path {
  fill: rgba(255, 255, 255, 0.801);
  stroke-width: 1.2px;
  stroke: none;
}
.btn-filters:hover path {
  fill: white;
}
.clear-filters {
  font-size: 16px;
  height: 43.6px;
}
.clear-filters svg {
  width: 18px;
  height: 18px;
}
.clear-filters:hover {
  color: white;
}
.filters section {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  position: relative;
  gap: 3.2px;
}
.section-expanded {
  display: grid !important;
  gap: 3.2px;
  width: 100%;
  grid-template-columns: repeat(auto-fill, 43.2px) !important;
  justify-content: space-between !important;
}
.filters-filter img.filters-filter-img {
  width: 27.2px;
  height: 27.2px;
  object-fit: contain;
}
.filters-filter {
  width: 43.2px;
  height: 43.2px;
  color: white;
  font-size: 19.2px;
  display: flex;
  box-sizing: border-box;
  justify-content: center;
  align-items: center;
  border-radius: 3.2px;
  cursor: pointer;
  position: relative;
  background: #25272D;
  transition: 0.3s;
}
.filters-filter-tooltip {
  display: block;
  position: absolute;
  width: max-content;
  max-width: 95px;
  text-align: center;
  background-color: #333;
  color: #fff;
  font-size: 14.4px;
  padding: 4px 6px;
  border-radius: 4px;
  z-index: 2;
  font-weight: 600;
  top: -1.6px;
  transform: translate(0, -150%);
  pointer-events: none;
  opacity: 0;
  transition: 0.4s;
  border: 1px solid black;
  box-sizing: border-box;
  will-change: opacity, tranform;
}
.filters-filter:hover {
  box-shadow: 0px 6.4px 13.6px 1.6px rgba(0, 0, 0, 0.14), 
              0px 2.4px 11.2px 1.6px rgba(0, 0, 0, 0.12), 
              0px 4px 4px -2.4px rgba(0, 0, 0, 0.2);
}
.filters-filter:hover .filters-filter-tooltip {
  opacity: 1;
  transform: translate(0, -100%);
}
.filters-filter--active {
  background: linear-gradient(180deg, #474A56 0%, rgba(71, 74, 86, 0.00) 193.52%);
}
.filters-header-btns {
  display: flex;
  align-items: center;
  justify-content: center;
  gap: 0.75rem;
  flex-wrap: wrap;
}
.filters-section--hidden {
  display: none !important;
}
@media (max-width: 810px) {
  .clear-filters {
    position: static;
    order: -1;
  }
  .filters-main-header {
    flex-direction: column;
    gap: 14px;
    margin-bottom: 0;
    justify-content: flex-start;
    align-items: flex-start;
  }
  .show-filters {
    display: flex;
    position: static;
    order: -1;
  }
  .filters section {
    display: none;
  }
  .filters-section {
    display: flex;
    flex-direction: row;
    flex-wrap: wrap;
    position: relative;
    gap: 3.2px;
  }
}
@media (max-width: 490px) {
  .filters-header-btns {
    flex-direction: column-reverse;
    gap: 14px;
    margin-bottom: 0;
    justify-content: flex-start;
    align-items: flex-start;
    width: 100%;
  }
  .btn-filters {
    width: 100%;
    justify-content: center;
  }
}
.MDcrafts-wrapper h2{
   font-size: 34px;
   margin: 0;
}
.MDcrafts-sign{
    font-size: 45px;
    display: flex;
    align-items: center;
}
.MDcrafts-line{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 10px;
    background-color: #131319;
    padding: 30px 50px;
    border-radius: 12px;
    box-sizing: border-box;
    margin-bottom: 15px;
}
.MDcrafts-req{
    display: flex;
    flex-wrap: wrap;
    align-items: center;
    gap: 15px;
}
.MDcrafts-result{
    display: flex;
    flex-direction: row;
}
.MDcrafts-wrapper img.MDcrafts-gift-img {
    width: 124px;
    height: 124px;
    object-fit: contain;
    background-color: #1d1d25;
}
.MDcrafts-wrapper picture{
    display: flex;
    flex-direction: column;
    text-align: center;
    font-size: 20px;
    cursor: pointer;
    position: relative;
    gap: 4px;
}
.MDcrafts-wrapper caption{
    width: 124px;
    text-overflow: ellipsis;
    box-sizing: border-box;
    padding: 0px 5px;
    font-weight: 500;
    font-size: 24px;
}
.GiftItem-shadow{
    width: 100%;
    height: 100%;
    top:0;
    left: 0;
    position: absolute;
    background-color: rgba(0, 0, 0, 0);
    transition: 0.3s;
    z-index: 3;
}
.GiftItem-img-wrapper{
    position: relative;
    width: 124px;
    height: 124px;
    transition: 0.3s;
    will-change: scale,transform;
    border-radius: 14px;
    overflow: hidden;
    z-index: 4;
}
.GiftItem-img-wrapper:hover .GiftItem-shadow{
    background-color: rgba(0, 0, 0, 0.5);
}
.GiftItem-info{
    position: absolute;
    display: flex;
    align-items: center;
    top:0;
    height: 100%;
    width: 100%;
    box-sizing: border-box;
    padding: 15%;
    text-align: center;
    color: white;
    z-index: 4;
    font-size: 24px;
    font-weight: bold;
    line-height: 20px;
    transform: scale(0);
    transition: 0.3s;
    will-change: scale transform;
}
.GiftItem-info--en{
    left: 0;
}
.GiftItem-info--ru{
    left: -8px;
}
.GiftItem-img-wrapper:hover .GiftItem-info{
    transform: scale(1);
}
.GiftItem-bar{
    position: absolute;
    height: 4px;
    width: 100%;
    bottom: 0;
    left: 0;
}
.GiftItem-bar--wrath{
    background-color: var(--wrath-frame-color);
}
.GiftItem-bar--lust{
    background-color: var(--lust-frame-color);
}
.GiftItem-bar--sloth{
    background-color: var(--sloth-frame-color);
}
.GiftItem-bar--glut{
    background-color: var(--glut-frame-color);
}
.GiftItem-bar--gloom{
    background-color: var(--gloom-frame-color);
}
.GiftItem-bar--pride{
    background-color: var(--pride-frame-color);
}
.GiftItem-bar--envy{
    background-color: var(--envy-frame-color);
}
.GiftItem-bar--none{
    background-color: var(--none-frame-color);
}
.GiftItem-img-wrapper .gift-keyword-img{
    position: absolute;
    width: 40px;
    height: 40px;
    right: 0px;
    bottom: 0px;
    object-fit: contain;
    z-index: 2;
    background-color: unset !important;
}
.GiftItem-img-wrapper .gift-tier{
    color:#fbc601;
    text-shadow: 0px 0px 10px red;
    font-size: 45px;
    font-weight: bold;
    position: absolute;
    left: 10px;
    top:5px;
}
@media (max-width:650px) {
    .MDcrafts-line{
        padding: 12px 20px;
    }
    .MDcrafts-wrapper img.MDcrafts-gift-img {
        width: 62px;
        height: 62px;
    }
    .GiftItem-img-wrapper{
        width: 62px;
        height: 62px;
    }
    .GiftItem-img-wrapper .gift-keyword-img{
        width: 20px;
        height: 20px;
    }
    .GiftItem-img-wrapper .gift-tier{
        font-size: 20px;
    }
    .MDcrafts-wrapper caption{
        width: 62px;
        font-size: 10px;
    }
    .GiftItem-info{
        font-size: 10px;
        line-height: 12px;
    }
}