.panel {
    position: fixed;
    top: 10px;
    right: 20px;
    background-color: #333;
    padding: 10px;
    border: 1px solid #444;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0, 0, 0, 0.5);
    z-index: 1000;
    width: 400px;
}

.panel-container {
    display: flex;
    flex-direction: column;
    align-items: center;
}

.input-group {
    width: 100%;
    margin-bottom: 20px;
}

.input-field {
    width: 90%;
    height: 30px;
    padding: 5px;
    margin-bottom: 10px;
    border: 1px solid #555;
    border-radius: 2px;
}

.hint-text {
    font-size: 14px;
    color: #ccc;
    margin-top: 5px;
}

.button-clear-cache {
    width: 100%;
    height: 35px;
    background-color: #007bff;
    color: white;
    border: none;
    border-radius: 2px;
    cursor: pointer;
    margin-bottom: 20px;
}

.button-clear-cache:hover {
    background-color: #0056b3;
}

.console-container {
    width: 100%;
    height: 150px;
    background-color: #464646;
    border: 1px solid #000000;
    border-radius: 5px;
    padding: 5px;
}

.console-header {
    background-color: #000000;
    padding: 2px;
    border-bottom: 1px solid #ddd;
    color: white;
}

.console-logs {
    padding: 2px;
    overflow-y: auto;
    max-height: calc(100% - 30px);
    color: white;
}

.console-logs p {
    margin-bottom: 2px;
}
